





































































import mpvuePicker from '../../../components/mpvue-picker/mpvuePicker.vue';
import mpvueCityPicker from '../../../components/mpvue-citypicker/mpvueCityPicker.vue'

export default {
	components: {
		mpvuePicker,
		mpvueCityPicker
	},
	data() {
		return {
			bank_card: {
				bank_code: '',
				bank_id:'',
				bank_name:'',
				phone:'',
				address:'',
				bank_child:'',
				provin:'',
				city:'',
				area:'',
				openid:'',
				sessionKey:'',
			},
			loading: false,
			themeColor: '#007AFF',
			cityPickerValueDefault: [0, 0, 0],
			pickerSingleArray:[],
			pickerValueDefault: [0],
			pickerValueArray:[],
			mode: '',
			deepLength: 1,
		}
	},
	onLoad() {this.bankInfo();},
	onShow() {},
	onShareAppMessage() {
		return {
			title: this.webshareTitle,
			path: '/pages/tabBar/index/index'
		}
	},
	computed: {},
	methods: {
		addBank(){
			//console.log(this.bank_card)
				this.bank_card.openid = this.$store.state.openid;
				this.bank_card.sessionKey = this.$store.state.sessionKey;
				const that = this;
				if(this.bank_card.bank_code==''){
					uni.showModal({
						content: '请输入银行卡号',
						showCancel: false
					});
					return;
				}
				
				if(this.bank_card.bank_name==''){
					uni.showModal({
						content: '请选择银行',
						showCancel: false
					});
					return;
				}
				
				if(this.bank_card.address==''){
					uni.showModal({
						content: '请选择省市',
						showCancel: false
					});
					return;
				}
				
				if(this.bank_card.bank_child==''){
					uni.showModal({
						content: '请输入支行名称',
						showCancel: false
					});
					return;
				}
				
				if(this.bank_card.phone==''){
					uni.showModal({
						content: '请输入银行预留手机号',
						showCancel: false
					});
					return;
				}
				
				
				
				try {
					if(this.loading){
						return;
					}
					this.loading = true;
					
					this.$http.post(this.websiteUrl + '/api/card/addBank', this.bank_card, (res) => {
						if(res.data.code==1){
							uni.showModal({
								content:res.data.msg,
								showCancel:false,
								success:function() {
									that.loading = false;
									uni.navigateBack();
								}
							})
						}else{
							uni.showModal({
								content:res.data.msg,
								showCancel:false,
								success:function() {
									that.loading = false;
								}
							})
						}
						
					});
				} catch (e) {
					uni.showModal({
						content: '网络异常,请稍后重试...',
						showCancel: false,
						success:function() {
							that.loading = false;
						}
					});
				}
		},
		bankInfo(){
			try {
				this.$http.post(this.websiteUrl + '/api/card/bankInfo', {
				}, (res) => {
					if (res.data.code == 1) {
						this.pickerSingleArray = res.data.result;
					}
				});
			} catch (e) {
				uni.showModal({
					content: '网络异常,请稍后重试...',
					showCancel: false
				})
			}
		},
		onCancel(e) {
			console.log(e)
		},
		// 单列
		showSinglePicker() {
			this.pickerValueArray = this.pickerSingleArray
			this.mode = 'selector'
			this.deepLength = 1
			this.pickerValueDefault = [0]
			this.$refs.mpvuePicker.show()
		},
		// 三级联动选择
		showMulLinkageThreePicker() {
			this.$refs.mpvueCityPicker.show()
		},
		onConfirmbank(e){
			if(e.value.length == 1){
				this.bank_card.bank_id = e.value[0];
				this.bank_card.bank_name = e.label;
			}
		},
		onConfirm(e) { 
			this.bank_card.provin = e.provinceCode;
			this.bank_card.city = e.cityCode;
			this.bank_card.area = e.areaCode;
			this.bank_card.address = e.label;
		},
		bindPickerChange: function(e) {
			console.log('picker发送选择改变，携带值为', e.target.value)
			this.index = e.target.value
		},

	},
	onBackPress() {
		if (this.$refs.mpvuePicker.showPicker) {
			this.$refs.mpvuePicker.pickerCancel();
			return true;
		}
		
		if (this.$refs.mpvueCityPicker.showPicker) {
			this.$refs.mpvueCityPicker.pickerCancel();
			return true;
		}
	},
	onUnload() {
		if (this.$refs.mpvuePicker.showPicker) {
			this.$refs.mpvuePicker.pickerCancel()
		}
		
		if (this.$refs.mpvueCityPicker.showPicker) {
			this.$refs.mpvueCityPicker.pickerCancel()
		}
	}
}
